	transform: inherit;
	box-shadow: inherit;
	background-color: inherit;
	.cMWoBu {
		background-color: #565656;
		position: relative;
		padding: 7px 20px 7px 10px;
		.PlTuF {
			position: absolute;
			top: 6px;
			right: 6px;
			&:hover {
				color: #fff;
			}
		}
	}
	input {
		background-color: #fff;
		font-family: inherit;

		&::placeholder {
			font-weight: 400;
			color: #aaa;
			font-style: inherit;
		}
	}
}

.default_bt {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	transition-duration: 0.2s;
	font-weight: 600;
	font-size: 15px;
	&.join_bt {
		border: 2px solid #fff;
		height: 66px;
		width: 227px;
		font-size: 18px;
		font-weight: 600;
		border-radius: 40px;
	}
	&.sns_submit_bt {
		background-color: #979da4;
		width: 100%;
		height: 56px;
		border-radius: 10px;
		color: #fff;
		&:hover {
			background-color: darken(#979da4, 10%);
		}
	}
	&.post_submit_bt {
		background-color: $primary_color;
		width: 100%;
		height: 56px;
		border-radius: 10px;
		color: #fff;
		&:hover {
			background-color: darken($primary_color, 10%);
		}
	}
	&.post_cancel_bt {
		margin-top: 30px;
		color: #aaaaaa;
		text-decoration: underline;
		font-size: 14px;
		font-weight: 600;
	}
	&.file_bt {
		width: 100px;
		height: 46px;
		background: #454545;
		border-radius: 4px;
		color: #fff;
	}
	&.go_to_price_bt {
		box-sizing: border-box;
		border: 1px solid #ddd;
		height: 64px;
		width: 100%;
		font-size: 17px;
		font-weight: 600;
		border-radius: 40px;
	}
	&.more_item_bt {
		border: 1px solid #ddd;
		padding: 15px 30px;
		border-radius: 4px;
		font-size: 16px;
		font-weight: 600;
		color: #aaa;
		background-color: #fff;
		&:hover {
			color: $black;
			border-color: #898989;
		}
	}
	&.price_sub_bt {
		border: 1px solid #ddd;
		padding: 0px 30px;
		height: 52px;
		border-radius: 4px;
		font-size: 16px;
		font-weight: 600;
		color: #898989;
		&:hover {
			color: $black;
			border-color: #898989;
		}
	}
	&.sns_share_bt {
		border: 1px solid #ddd;
		padding: 0px 20px;
		height: 52px;
		border-radius: 4px;
		font-size: 16px;
		font-weight: 600;
		color: #898989;
		&:hover {
			color: $black;
			border-color: #898989;
		}
	}
	&.price_buy_bt {
		background-color: $primary_color;
		font-size: 16px;
		padding: 0px 34px;
		height: 52px;
		border-radius: 4px;
		font-weight: 600;
		color: #fff;
		&.price_cancel {
			background-color: #aaa;
			&:hover {
				background-color: #ccc;
			}
		}
		&:hover {
			background-color: darken($primary_color, 10%);
		}
	}
	&.pc_reset_bt {
		color: #898989;
		text-decoration: underline;
		font-size: 16px;
		font-weight: 600;
		&:hover {
			color: #565656;
		}
	}
	&.price_compare_bt {
		background-color: #282828;
		font-size: 16px;
		padding: 0px 34px;
		height: 52px;
		border-radius: 4px;
		font-weight: 600;
		color: #fff;
		&.disable_bt {
			background-color: #ccc;
			&:hover {
				background-color: #ccc;
			}
		}
		&:hover {
			background-color: #181818;
		}
	}
	&.pop_check_bt {
		width: 110px;
		height: 46px;
		background-color: #454545;
		color: #fff;
		border-radius: 4px;
		font-size: 15px;
		&:hover {
			background-color: #353535;
		}
	}
	&.sns_login_bt {
		width: 420px;
		height: 70px;
		border-radius: 70px;
		background-color: #fff;
		border: 1px solid #eee;
		font-weight: 500;
		img {
			height: 20px;
			margin-right: 15px;
		}
		&:hover {
			border-color: #898989;
		}
	}
}
@media only screen and (max-width: 1500px) and (min-width: 1350px) {
	.contents_wrap {
		.filter_section {
			align-items: flex-start;
			padding: 15px 60px;
			height: inherit;
			.sub_filter_section {
				flex-wrap: wrap;
				.ft_tags {
					margin-top: 10px;
					margin-left: 0px;
					width: 100%;
					.tag_item {
						padding: 0px;
						margin-right: 15px;
					}
				}
			}
		}
	}
}
@media only screen and (max-width: 1350px) and (min-width: 900px) {
	.header {
		.search_section {
			input {
				width: 240px;
			}
		}
		.main_menu {
			& > ul > li {
				margin-left: 20px;
			}
		}
	}
	.contents_wrap {
		.filter_section {
			.ft_process {
				.ftp_item {
					.ftp_num {
						border: 1px solid #ddd;
						border-radius: 50%;
						width: 24px;
						height: 24px;
						display: inline-block;
						text-align: center;
						line-height: 24px;
					}
					img {
						opacity: 0.5;
					}
					.ftp_t {
						display: none;
					}
				}
			}
		}
		.intro {
			&.intro3 {
				.deco_img {
					img {
						width: 350px;
					}
				}
			}
			&.intro5 {
				padding: 160px 30px;
				.price_items {
					.pr_item {
						.pr_tags {
							flex-direction: column;
							.pr_tag {
								margin-bottom: 10px;
								text-align: center;
							}
						}
						.pr_contents {
							.pr_desc {
								margin-top: 20px;
							}
						}
					}
				}
			}
		}
		.tutorial_items {
			.tt_item {
				width: calc(33.3% - 30px);
				.tt_contents {
					padding: 12px 15px;
					.tt_bottom {
						padding: 0px 15px;
						bottom: 10px;
					}
				}
			}
		}
		.price_section {
			.price_items {
				.price_item {
					padding: 20px 30px;
				}
			}
		}
		.price_detail_section {
			.pr_container {
				min-width: 1100px;
			}
		}
		.default_bt {
			&.price_sub_bt {
				padding: 0px 15px;
			}
		}
	}
}

.tab_creatorMarket {
	position: relative;
}
.tab_creatorMarket:hover {
	.creatorMarket_dropdown {
		font-family: 'Roboto', 'Noto Sans KR', 'Noto Sans', sans-serif;
		position: absolute;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		top: 17px;
		left: -15%;
		width: 170px;
		padding: 15px 0px;
		text-align: left;
		filter: drop-shadow(0px 2px 10px rgba(0, 0, 0, 0.1));
		background-color: #fff;
		font-weight: 700;
		border-radius: 15px;
		z-index: 1000;
		border: 1px solid #eee;
		color: $black;

		a {
			width: 122px;
			line-height: 36px;
			padding: 0px 25px;
			cursor: pointer;
			position: relative;
			font-size: 15px;

			&:hover {
				background-color: #eee;
				color: $black;
			}
		}
	}
}
.creatorMarket_dropdown {
	font-weight: 500;
	display: none;
}
